'use strict';

var Util = require('@ali/g-util');
var Guide = require('@ali/g-guide');
var Global = require('../global');
var FRONT_TYPES = ['text', 'tag', 'html']; // 

var GuideAssist = function(cfg) {
  this.guides = [];
  this.options = [];
  Util.mix(this, cfg);
};

Util.augment(GuideAssist, {
  guides: null,
  xScale: null,
  yScale: null,
  backPlot: null, // canvas
  frontPlot: null, // canvas
  options: [],

  _addGuide: function(guide) {
    this.guides.push(guide);
  },
  _getDefault: function() {
    return {
      xScale: this.xScale,
      yScale: this.yScale
    };
  },
  setScale: function(xScale, yScale) {
    var guides = this.guides;
    this.xScale = xScale;
    this.yScale = yScale;
    Util.each(guides, function(guide) {
      guide.xScale = xScale;
      guide.yScale = yScale;
    });
  },
  _setOptions: function(tmpOptions) {
    var options = this.options;
    options.push(tmpOptions);
  },
  creatGuide: function() {
    var self = this;
    var types = ['line', 'text', 'rect', 'arc', 'tag', 'html'];
    var options = this.options;
    options.forEach(function(option) {
      var type = option.type;
      if (types.indexOf(option.type) !== -1) {
        option.cfg = Util.mix({}, Global.guide[type], option.cfg);
      }
      type = Util.ucfirst(type);
      var config = Util.mix({}, self._getDefault(), option);
      var guide = new Guide[type](config);
      self._addGuide(guide);
    });
  },
  /**
   * 
   * @chainable
   * @param  {Array} from 
   * @param  {Array} to   
   * @param  {Object} cfg  
   */
  line: function(from, to, cfg) {
    var config = {
      type: 'line',
      from: from,
      to: to,
      cfg: cfg
    };

    this._setOptions(config);
    return this;
  },
  /**
   * 
   * @chainable
   * @param  {Array} position 
   * @param  {String} text   
   * @param  {Object} cfg  
   */
  text: function(position, text, cfg) {
    var config = {
      type: 'text',
      position: position,
      text: text,
      cfg: cfg
    };

    this._setOptions(config);
    return this;
  },
  /**
   * 
   * @chainable
   * @param  {Array} start 
   * @param  {Array} [end] 
   * @param  {Object} cfg  
   */
  image: function(start, end, cfg) {
    var config = {
      type: 'image',
      start: start
    };
    if (Util.isArray(end)) {
      config.end = end;
    }
    if (Util.isObject(end)) {
      cfg = end;
    }
    if (cfg) {
      config.cfg = cfg;
    }
    this._setOptions(config);
    return this;
  },
  /**
   * 
   * @chainable
   * @param  {Array} start 
   * @param  {Array} end 
   * @param  {Object} cfg  
   */
  rect: function(start, end, cfg) {
    var config = {
      type: 'rect',
      start: start,
      end: end,
      cfg: cfg
    };

    this._setOptions(config);
    return this;
  },
  /**
   * 
   * @chainable
   * @param  {Array} start 
   * @param  {Array} end 
   * @param  {Object} cfg  
   */
  arc: function(start, end, cfg) {
    var config = {
      type: 'arc',
      start: start,
      end: end,
      cfg: cfg
    };

    this._setOptions(config);
    return this;
  },
  /**
   * 
   * @chainable
   * @param  {Array} from 
   * @param  {Array} to   
   * @param {String} text 
   * @param  {Object} cfg  
   */
  tag: function(from, to, text, cfg) {
    var config = {
      type: 'tag',
      from: from,
      to: to,
      text: text,
      cfg: cfg
    };

    this._setOptions(config);
    return this;
  },
  /**
   * html
   * @chainable
   * @param  {Array} from 
   * @param  {Array} to   
   * @param {String} text 
   * @param  {Object} cfg  
   */
  html: function(point, html, cfg) {
    var config = {
      type: 'html',
      point: point,
      html: html,
      cfg: cfg
    };

    this._setOptions(config);
    return this;
  },
  /**
   * 
   * @param  {Coord} coord 
   * @param  {Canvas.Group} group 
   */
  paint: function(coord, group, frontGroup) {
    var guides = this.guides;
    group = group || this.backPlot;
    frontGroup = frontGroup || this.frontPlot;
    Util.each(guides, function(guide) {
      if (Util.inArray(FRONT_TYPES, guide.type)) {
        guide.paint(coord, frontGroup);
      } else {
        guide.paint(coord, group);
      }
    });
  },
  //  guide 
  clear: function() {
    this.options = [];
    this.reset();
  },
  // 
  reset: function() {
    this.guides = [];
    var frontPlot = this.frontPlot;
    if (frontPlot && !frontPlot.get('destroyed')) {
      var parent = frontPlot.get('parent') ? frontPlot.get('parent').get('el').parentNode : frontPlot.get('el').parentNode;
      var guideWrpper = parent.getElementsByClassName('guideWapper')[0];
      guideWrpper && parent.removeChild(guideWrpper);
    }
  }
});

module.exports = GuideAssist;



